.card__front {
  position: relative;
  transform: perspective(600px) rotateY(0deg);
  backface-visibility: hidden;
  transition: transform $trans-800;
}

.card__front > h3 {
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  position: absolute;
  margin: 0;
  color: $color-alice-blue;
  bottom: 22px;
  left: 30px;

  @include vp-768 {
    bottom: 27px;
    left: 35px;
  }

  @include vp-320 {
    bottom: 18px;
    left: 26px;
  }
}

.card__front > img {
  width: 260px;
  height: 386px;

  @include vp-768 {
    width: 309px;
  }

  @include vp-320 {
    width: 270px;
  }
}
